import 'isomorphic-fetch';

import {
    INDEXNEWS_REQUEST,
    INDEXNEWS_RECEIVE,
} from '../constants/indexNews';

export const indexNewsRequest = n => ({
    type: INDEXNEWS_REQUEST,
    amount: n
});

export const indexNewsReceive = (n, stories) => ({
    type: INDEXNEWS_RECEIVE,
    amount: n,
    data: stories
});

export const indexNewsPost = n => (dispatch) => {
    dispatch(indexNewsRequest(n));
    return fetch('http://0.0.0.0:9527/api')
    .then((response) => {
        if (response.status >= 400) {
            console.log('net work fail');
        }
        return response.json();
    })
    .then((json) => {
        dispatch(indexNewsReceive(n, json));
    }).catch(ex =>
        console.log('parsing failed', ex)
    );
};



